import { useEffect, useState } from "react";
import { useUserDataContext } from "../../../UserContext";

const useReservationsFromTabs = () => {
    const { Auth } = useUserDataContext();
    const [idToken, setIdToken] = useState("");

    const authStorage = Auth.user?.storage;

    useEffect(() => {
        if (authStorage) {
            Object.keys(authStorage).map(key => {
                if (key.endsWith("idToken")) {
                    setIdToken(authStorage[key]);
                }
            });
        }
    }, [authStorage])

    const getIdToken = async ():Promise<string> =>{
        let token =""
        if (authStorage) {
            Object.keys(authStorage).map((key) => {
                if (key.endsWith("idToken")) {

                    setIdToken(authStorage[key]);
                    token = authStorage[key];
                }
            });
        }
        return token;
    }

    return { idToken, getIdToken };
};

export default useReservationsFromTabs;
